nd-size: cover;\n    background-repeat: no-repeat;\n    width: 100%;\n    height: 100%;\n}\n#overlay{\n    display: none;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    z-index: 9999;\n}\n\n@media @mobile_landscape {\n    .container{\n        top: 50%;\n        width: 95%;\n        height: 550px;\n    }\n}\n\n@media @mobile_portrait {\n    .container{\n        top: 50%;\n        width: 95%;\n        height: 700px;\n    }\n}","@font-face {\n  font-family: 'Indie Flower';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/indieflower/v12/m8JVjfNVeKWVnh3QMuKkFcZlbg.ttf) format('truetype');\n}\n","@font-face {\n  font-family: 'RocknRoll One';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/rocknrollone/v7/kmK7ZqspGAfCeUiW6FFlmEC9guU.ttf) format('truetype');\n}\n","@font-face {\n  font-family: 'Orbitron';\n  font-style: normal;\n  font-weight: 700;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/orbitron/v19/yMJMMIlzdpvBhQQL_SC3X9yhF25-T1ny_Cmxpg.ttf) format('truetype');\n}\n","@import \"./fonts.less\";\n@import \"./colors.less\";\n\n.border(@width, @color, @radius: 0) {\n    border: @width solid @color;\n    border-radius: @radius;\n}\n\n.center() {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n}\n\n.buttons {\n    display: flex;\n    justify-content: space-around;\n    position: absolute;\n    width: 100%;\n    bottom: 10%;\n    right: 0;\n    text-align: center;\n\n    button {\n        .button;\n    }\n}\n\n.button {\n    display: inline-block;\n    background-color: #colors[@optionAndEnd-button-background-color];\n    padding: 10px 30px 15px;\n    border-radius: 22px;\n    color: white;\n    font-size: 1.7rem;\n    border: none;\n    outline: none;\n    font-family: inherit;\n    min-width: 250px;\n\n    &:hover {\n        cursor: pointer;\n        background-color: #colors[@optionAndEnd-button-hover-background-color];\n    }\n}\n\n.option {\n    display: inline-block;\n    width: 135px;\n    height: 135px;\n    position: relative;\n    cursor: pointer;\n    margin: 0 10px;\n    border-radius: 6px;\n    opacity: .55;\n}\n\n@mobile_landscape: ~\"all and (orientation: landscape) and (max-device-width: 800px)\";\n@mobile_portrait: ~\"all and (orientation: portrait) and (max-device-width: 800px)\";\n","@keyframes show {\n    from {\n        opacity: 0;\n    }\n\n    to {\n        opacity: 1;\n    }\n}\n\n@keyframes hide {\n    from {\n        opacity: 1;\n    }\n\n    to {\n        opacity: 0;\n        cursor: default\n    }\n}\n\n@keyframes squintingTimer {\n    from {\n       color: inherit\n    }\n\n    to {\n       color: #colors[@score-squinting-stats-font-color]\n    }\n}\n\n@keyframes closeTheGateLeft {\n    from {\n        left: -100%\n    }\n\n    to {\n        left: -45%\n    }\n}\n\n@keyframes closeTheGateRight {\n    from {\n        right: -100%\n    }\n\n    to {\n        right: -45%\n    }\n}\n\n@keyframes openTheGateLeft {\n    from {\n        left: -45%\n    }\n\n    to {\n        left: -100%\n    }\n}\n\n@keyframes openTheGateRight {\n    from {\n        right: -45%\n    }\n\n    to {\n        right: -100%\n    }\n}",".gate(){\n    position: absolute;\n    z-index: 1000;\n    width: 100%;\n    height: 100%;\n    background-size: cover;\n    .border(1px, black); \n    display: inline-block;\n    margin: auto;\n}\n#left_gate{\n    .gate;\n    background-color: black;\n    left: -100%;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 90% (100%/9*5), 100% (100%/9*6),\n    90% (100%/9*7), 100% (100%/9*8),\n    90% 100%, 0 100%);\n}\n#left_gate::before{\n    content: \"\";\n    .gate;\n    right: 10px;\n    z-index: 999;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 